import axios from 'axios'
import qs from 'qs'
import store from '@/store'
// import { MessageBox,Popup} from "mint-ui";
// import { getToken } from '@/utils/auth'

// create an axios instance
const service = axios.create({
  baseURL: process.env.REDPACK_BASE_API, // api的base_url
  timeout: 5000, // request timeout
  headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
  withCredentials: true,
  transformRequest: [(data) => {
    // data['xhrFields'] = { withCredentials: true }
    return qs.stringify(data)
  }]
})

// request interceptor
service.interceptors.request.use(
  config => {
    // Do something before request is sent
    if (store.getters.token) {
      // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
      // config.headers['Authorization'] = getToken()
    }
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)
let SESSION_EX_DIALOG_FLAG = false;
// respone interceptor
service.interceptors.response.use(
  response => {
    if(response.status == 200 && response.data.code == 10)
    {
      if(!SESSION_EX_DIALOG_FLAG)
      {
        SESSION_EX_DIALOG_FLAG = true
        setTimeout(() => {
          // alert("没有登录");
          // window.location.href="#/login";
        }, 3000);
        setTimeout(() => {
          SESSION_EX_DIALOG_FLAG = false;
        }, 5000);
      }
    }

    return response;
  },
  /**
   * 下面的注释为通过在response里，自定义code来标示请求状态
   * 当code返回如下情况则说明权限有问题，登出并返回到登录页
   * 如想通过xmlhttprequest来状态码标识 逻辑可写在下面error中
   * 以下代码均为样例，请结合自生需求加以修改，若不需要，则可删除
   */
  // response => {
  //   const res = response.data
  //   if (res.code !== 20000) {
  //     Message({
  //       message: res.message,
  //       type: 'error',
  //       duration: 5 * 1000
  //     })
  //     // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
  //     if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
  //       // 请自行在引入 MessageBox
  //       // import { Message, MessageBox } from 'element-ui'
  //       MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
  //         confirmButtonText: '重新登录',
  //         cancelButtonText: '取消',
  //         type: 'warning'
  //       }).then(() => {
  //         store.dispatch('FedLogOut').then(() => {
  //           location.reload() // 为了重新实例化vue-router对象 避免bug
  //         })
  //       })
  //     }
  //     return Promise.reject('error')
  //   } else {
  //     return response.data
  //   }
  // },
  error => {
    return Promise.reject(error.response)
  }
)

export default service



